.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
  margin: 5vw;
}

.App-wow {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s linear;
  }
  .bitch{
    animation: bitch-color infinite 3s;
  }
}
.bitch{
  color: hotpink;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes bitch-color {
  0%{
    color: blueviolet;
    font-size: 3vw;
  }
  50% {
    font-size: 7vw;
    color: purple;
  }
  100%{
    color: blueviolet;
    font-size: 3vw;
  }
}
